.navigation__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navigation__item {
  letter-spacing: -0.1px;
}

.navigation__item::after {
  content: "/";
  display: inline-block;
  color: #91919f;
  margin-right: 18px;
  margin-left: 18px;
  opacity: 0.3;
}

.navigation__item:last-child::after {
  display: none;
}

.navigation__link {
  color: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.42;
}

.navigation__list_second .navigation__item::after {
  display: none;
}
